import { adapters } from "@rp/adapters"
import { ConfigEnvs } from "@rp/common/environment/types"
import { initDatadog } from "./datadog"
import { initGoogleAnalytics } from "./google-analytics"
import { initHotjar } from "./hotjar"
import { initSentry } from "./sentry"
import { initTagManager } from "./tag-manager"
import { GoogleAnalyticsConfig, HotjarConfig, SentryConfig, TagManagerConfig } from "./types"

type Config = {
  tagManager: TagManagerConfig
  googleAnalytics: GoogleAnalyticsConfig
  hotjar: HotjarConfig
  sentry: SentryConfig
  environment: ConfigEnvs
}

const initializeExternalTools = ({
  tagManager,
  googleAnalytics,
  hotjar,
  sentry,
  environment,
}: Config) => {
  initSentry(sentry, environment)
  initTagManager(tagManager)
  initHotjar(hotjar)
  initGoogleAnalytics(googleAnalytics)
  adapters.customerIo.init(environment)

  if (["development", "staging", "production"].includes(environment)) {
    initDatadog(environment)
  }
}

export { initializeExternalTools }
